/* src/styles.css */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .homepage {
    text-align: center;
    padding: 20px;
  }
  
  .featured-products {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  
  .featured-product {
    width: 200px;
    margin: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .featured-product img {
    width: 100%;
    height: auto;
  }
  
  .featured-product h3 {
    margin: 10px 0;
  }
  
  .featured-product button {
    background-color: #4caf50;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  